.nav {
  position: fixed;
  top: 0;
  width: 100%;
  display: flex;
  justify-content: space-between;
  padding: 10px;
  height: 30px;
  background-color: black;
}
.nav__logo {
  width: 80px;
  left: 0px;
  position: fixed;
  object-fit: contain;
}
.nav__avatar {
  width: 30px;
  right: 20px;
  position: fixed;
  object-fit: contain;
}
